  --popup-left-offset: 50%;
  font-size: 10px;
}

body {
  margin: 0;
  font-family: "Montserrat", Arial, sans-serif;
  text-rendering: optimizeLegibility;
  background: url(/portfolio-manager/static/media/background.c795ad8c.jpg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: auto;
}

