.popup-title-icon {
  margin-right: 1rem;
}

.popup-description {
  font-size: 1.2rem;
  padding: 1.2rem;
  line-height: 1.6rem;
}

/* Popup menu styles */

.popup-menu-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
